*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'montserrat', sans-serif;
    background-color: #223343;
}
header{
    padding: 50px 15px 0px;
}

header h1{
    color: #fff;
    font-size: 50px;
    font-weight: 500;
    text-align: center;
}

main{
    width: 100%;
    max-width: 960px;
    margin: auto;
    overflow-y: hidden;
}

section{
    padding: 15px;
}
.search{
    padding: 15px;
    display: flex;
}

.searchbox{
    display: block;
    width: 80%;
    padding: 15px;
    border: 1px solid #000000;
    border-right: none;
    border-top-left-radius: 10px;
    -webkit-border-bottom-left-radius: 10px;
    border-bottom-left-radius: 10px ;
    -webkit-border-top-left-radius: 10px;
    -moz-border-top-left-radius: 10px;
    -moz-border-bottom-left-radius: 10px ;
    background:none;
    background-color: white;
    outline: none;
    color: #535654;
    font-size: 2opx;
    font-weight: 300;
    transition: 0.4s ease-out;
}

.button{
    width: 20%;
    color: white;
    padding: 10px;
    background-color: orangered;
    border: 1px solid #000000;
    border-top-right-radius: 10px;
    -webkit-border-bottom-right-radius: 10px;
    border-bottom-right-radius: 10px ;
    -webkit-border-top-right-radius: 10px;
    -moz-border-top-right-radius: 10px;
    -moz-border-bottom-right-radius: 10px ;
    outline: none;
}

.searchbox:focus{
    box-shadow: 0px 0px 4px 2px rgb(202, 179, 171);
}

.results {
    display: flex ;
    flex-wrap: wrap;
     margin:8px;
}

.results .result {
    width: 30%;
     margin:9px;
    min-width: 150px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    
}
.results .result img {
    display: block;
    width: 280px;
    
    border-radius: 5px;

}

.results .result h3{
    color: #fff;
    font-size: 20px;
    font-weight: 600;
    padding: 10px;
    background: #445565 ;
    width: 280px !important;
}

.results >.result:hover h3{
    background: #fff !important;
    color: #445565 !important;
    
}

.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    overflow-y: scroll;
}

.popup .content {
    display: block;
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
    padding: 25px;
    color: #223343;
}
.popup .content h2 {
    font-size: 30px;
    font-weight: 600;
}

.popup .content h2 span {
    margin-bottom: 30px;
}

.popup .content .plot{
    display: flex;
    flex-wrap: wrap;
    margin: 0px -15px 30px;
}

.popup .content .plot img {
    flex: 1 1 50%;
    max-width: 300px;
    padding: opx 15px;
}

.popup .content .plot p {
    flex: 1 1 50%;
    padding: opx 15px;
}
.popup .content .close {
    display: inline-block;
    padding: 15px 30px;
    font-size: 10px;
    font-weight: 700;
    background-color: #4484c4;
    color: white;
    border-radius: 8px;
    border: none;
    outline: none;
    appearance: none;
    transition: 0.4s ease-out;


}

.not-found {
    color: white;
    font-size: 30px;
    font-weight: 400;
    text-align: center;
}

.popup .content .close:hover_{
    background: #221343;
}


 @media only screen and (max-width: 600px) {
     img{
         height: 300px;
         width: 200px;
     }
     .results .result {
         width: 40%;
     }
     .results .result img {
        display: block;
        width: 150px;
        
        border-radius: 5px;
    
    }
 }